'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { io, Socket } from 'socket.io-client';
import { useRef } from 'react';

interface Message {
    sender: string;
    content: string;
    timestamp: string;
}

interface Conversation {
    id: string;
    email: string;
}

export default function MessagesPage() {
    const socketRef = useRef<Socket | null>(null);
    const [conversations, setConversations] = useState<Conversation[]>([]);
    const [selectedConversation, setSelectedConversation] = useState<Conversation | null>(null);
    const [messages, setMessages] = useState<Message[]>([]);
    const [newMessage, setNewMessage] = useState('');
    const [allUsers, setAllUsers] = useState<Conversation[]>([]);
    const [showUserList, setShowUserList] = useState(false);
    const [currentUserEmail, setCurrentUserEmail] = useState('');
    const router = useRouter();

    // labeling messages
    useEffect(() => {
        const token = localStorage.getItem('token');
        if (!token) return;

        const fetchMe = async () => {
            const res = await fetch('http://localhost:6543/api/me', {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });
            const data = await res.json();
            setCurrentUserEmail(data.email);

            if (!socketRef.current) {
                socketRef.current = io('http://localhost:6543');
            }

            socketRef.current.emit('register', data.id);

            socketRef.current.on('receiveMessage', (message) => {
                setMessages((prev) => [...prev, message]);
            });
        };

        fetchMe();

        return () => {
            socketRef.current?.disconnect();
        }
    }, []);

    // fetch possible new users to talk to
    useEffect(() => {
        // fetch users only once on mount
        const token = localStorage.getItem('token');
        if (!token) {
            router.push('/login');
            return;
        }

        const fetchUsers = async () => {
            const res = await fetch('http://localhost:6543/api/users', {
                headers: { Authorization: `Bearer ${token}` },
            });
            const data = await res.json();
            setAllUsers(data.users);
        };

        fetchUsers();
    }, []);

    // fetch conversations when the page loads
    useEffect(() => {
        const token = localStorage.getItem('token');
        if (!token) {
            router.push('/login');
            return;
        }

        const fetchConversations = async () => {
            const res = await fetch('http://localhost:6543/api/conversations', {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });
            const data = await res.json();
            setConversations(data.conversations);
        };

        fetchConversations(); // initial load
        const interval = setInterval(fetchConversations, 2000); // update every 5 seconds

        return () => clearInterval(interval);
    }, []);

    // fetch messages when a conversation is selected
    useEffect(() => {
        if (!selectedConversation) return;

        const fetchMessages = async () => {
            const res = await fetch(`http://localhost:6543/api/conversations/${selectedConversation.id}/messages`, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            });

            const data = await res.json();
            setMessages(data.messages);
        };

        fetchMessages();
        const interval = setInterval(fetchMessages, 2000); // fetch every 10 seconds

        return () => clearInterval(interval);

    }, [selectedConversation]);

    // handle when user sends a new messages in current conversation
    const handleSendMessage = async () => {
        if (newMessage.trim()) {
            const res = await fetch(`http://localhost:6543/api/conversations/${selectedConversation?.id}/messages`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                },
                body: JSON.stringify({ content: newMessage }),
            });

            if (res.ok) {
                const messageData = {
                    sender: currentUserEmail,
                    content: newMessage,
                    timestamp: new Date().toISOString(),
                };

                // Update local UI immediately
                setMessages((prevMessages) => [...prevMessages, messageData]);

                // Emit message to recipient via socket
                socketRef.current?.emit('sendMessage', {
                    ...messageData,
                    recipientId: selectedConversation?.id,
                });

                setNewMessage('');
            }
        }
    };

    return (
        <div className="flex min-h-screen">
            <div className="w-1/4 p-4 border-r">
                <button
                    className="mb-4 bg-green-500 text-white py-1 px-3 rounded hover:bg-green-600"
                    onClick={() => setShowUserList(!showUserList)}
                >
                    New Message
                </button>

                {showUserList && (
                    <ul className="mb-4 max-h-40 overflow-y-auto border rounded p-2 bg-black">
                        {allUsers.map((user) => (
                            <li
                                key={user.id}
                                onClick={() => {
                                    setSelectedConversation(user);
                                    setShowUserList(false);
                                }}
                                className="cursor-pointer hover:bg-gray-100 p-1 rounded"
                            >
                                {user.email}
                            </li>
                        ))}
                    </ul>
                )}
                <h2 className="text-2xl font-semibold mb-4">Conversations</h2>
                <ul className="mb-4 max-h-40 overflow-y-auto border rounded p-2 bg-black">
                    {conversations.map((conversation) => (
                        <li
                            key={conversation.id}
                            onClick={() => setSelectedConversation(conversation)}
                            className="cursor-pointer hover:bg-gray-200 p-2 rounded mb-2"
                        >
                            {conversation.email ?? conversation.email ?? 'Unnamed'}
                        </li>
                    ))}
                </ul>
            </div>

            <div className="flex-1 p-4">
                {selectedConversation ? (
                    <>
                        <h2 className="text-2xl font-semibold mb-4">{selectedConversation.email}</h2>
                        <div className="space-y-4 h-[60vh] overflow-y-scroll border-b pb-4">
                            {messages.map((message, index) => (
                                <div key={index} className="flex flex-col">
                                    <div className="font-semibold text-gray-600">
                                        {message.sender === currentUserEmail ? 'You' : message.sender}
                                    </div>
                                    <div>{message.content}</div>
                                    <div className="text-sm text-gray-400">{new Date(message.timestamp).toLocaleString()}</div>
                                </div>
                            ))}
                        </div>
                        <div className="mt-4">
                            <input
                                type="text"
                                value={newMessage}
                                onChange={(e) => setNewMessage(e.target.value)}
                                className="w-full p-2 border rounded"
                                placeholder="Type a message..."
                            />
                            <button
                                onClick={handleSendMessage}
                                className="w-full bg-blue-500 text-white py-2 mt-2 rounded hover:bg-blue-600"
                            >
                                Send
                            </button>
                        </div>
                    </>
                ) : (
                    <div className="text-center">Select a conversation to view messages</div>
                )}
            </div>
        </div>
    );
}
